// -----------------------------------------------------------------------------
// This file contains very basic styles.
// -----------------------------------------------------------------------------

*{
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before{
  box-sizing:inherit;
}

html {
  font-size: 62.5%;
  box-sizing:border-box;
  @media(max-width:62em){
    font-size: 56.25%;
  }
  @media(max-width:48em){
    font-size: 50%;
  }

}

.page-wrapper{
  max-width: 120em;
  margin: 0 auto;
  overflow-x: hidden;
  overflow-y: auto;
  padding-left: 2%;
  padding-right: 2%;
}

body {
  line-height: 1.4;
  overflow-x: hidden;
  font-family: var(--font-book);
  font-size: 1.6rem;
  height: 100vh;
  // background-color: var(--color-secondary);

}



.container{
 max-width: 150rem;
 padding: 0 4rem;

//  @media(max-width:93.75em){
//   max-width:120rem;
// }

//  @media(max-width:62em){
//    max-width: 100%;
//  }

 @media(min-width:120em){
  max-width: 150rem;
}

}



a,
a:link {
  text-decoration: none;
}

ul{
  list-style: none;
  padding: 0;
}




.select2-container{
  .select2-dropdown{
      background-color: white;
      border: none;
      box-shadow: 2px 10px 10px #00000040;

  }
  .select2-results{
      ul{
          font-size: 1.6rem;
          .select2-results__option{
              padding: 0 1.5rem;
              line-height: 5rem;
              color: var(--color-black);
              &[aria-selected="true"]{
                  background: var(--color-black);
                  color: white;
                  &.select2-results__option--highlighted{
                      background: var(--color-red);
                  }
              }
          }
          .select2-results__option--highlighted[aria-selected]{
              background-color: rgb(226, 226, 226);
          }
      }
  }
}



#section1{
